@import "../base/variables.scss";


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $ff-primary;
}

  
  a {
    text-decoration: none;
  }

  a:focus  {
    background: $clr-secondary!important;
    border-color: $clr-secondary!important;
    color: $clr-primary!important;
  }

  .whitening-btn:focus,
  .implants-btn:focus,
  .vineers-btn:focus,
  .facial_aesthetics-btn:focus{
    background: $clr-primary!important;
    color: white!important;
  }






ul , ol {
  list-style: none;
}


.section-title {
  font-size: $font-md;
  font-weight: 900;
  color: $clr-primary;
}

